				"py": "Wng losh: Ybn jitng sh bgu shngdn ji de. Yu de rngu shngdn ji, knng sh xhun shngdn ji n zhng hunl de qfn, hizimen nng cng bba mm ner ddo lw, dngrn y hn goxng. Bgu, zhnggu zud de jir hish chnji.",
				"vn": "Thy Vng: Thng thng cc gia nh khng n Noel.\r\nC mt s ngi n Noel, c l l thch bu khng kh vui v an lnh ca tt Noel, cn cc chu c th nhn c qu t ch b m, ng nhin cng rt vui.\r\nC iu, ngy l tt ln nht  Trung Quc vn l tt Nguyn n.",
				"from": 259.791,
