		"subtitles": [
			{
				"zh": "",
				"py": "q zuk",
				"vn": "Lm khch",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Zutin wmen do tin fng ji q zuk.",
				"vn": "Hm qua chng ti n nh in Phng lm khch",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Q de shhu, y xi d hn d, bgu wmen y do tin fng ji y ji tngle.",
				"vn": "Lc i, tri ma rt to, nhng chng ti va n nh in Phng th tnh ma.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng jizh zi yg shyun l.",
				"vn": "Nh in Phng  trong mt ci T hp vin.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Yunzi l zhngzhe y k zo sh, hi zhngzhe hndu hu, yu hng de, yu hung de, fichng pioliang.",
				"vn": "Trong sn c trng mt cy to, cn trng rt nhiu hoa, c hoa mu , c hoa mu vng, v cng p.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tmen ji de yunzi b ti d, dnsh hn gnjng, y hn njng.",
				"vn": "Khu nh h khng to lm, nhng rt sch, cng rt yn tnh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng de bba mm y kn wmen lile, ji zu chli xiozhe shu:",
				"vn": "B m in Phng va thy chng ti n, lin i ra va ci va ni:",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Hunyng nmen li wji zuk, qng do w li zu ba.",
				"vn": "Hoan nghnh cc chu n nh ti chi, mi vo nh ngi i.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng gi wmen jishole t de bba mm.",
				"vn": "in Phng gii thiu b m ca c y vi chng ti.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W zhdo tin fng de bba mm du sh dxu jioshu.",
				"vn": "Ti bit b m ca in Phng u l gio s ca trng i hc.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng de bba shu, nmen ji jio wmen tin losh h hung losh ba.",
				"vn": "B ca in Phng ni, cc chu c gi chng ti l thy in v c Hong i.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Hung losh rqng de gi wmen do ch, qng wmen ch shugu.",
				"vn": "C Hong rt nhit tnh rt tr cho chng ti, mi chng ti n hoa qu.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T shu, nmen gn tin fng sh tngxu, yu sh ho pngyu, do zher li ji xing do zj jil yyng, byo kq.",
				"vn": "C y ni, cc em l bn hc ca in Phng, li l bn thn, n y ri th c coi nh l n nh mnh vy, ng khch so.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng shu, xin cngun cngun w bba de shfng ba.",
				"vn": "in Phng ni, thm quan mt cht th phng ca b t trc nh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T lngzhe wmen zu jnle t bba de shfng.",
				"vn": "C y dn chng ti vo th phng ca b c y.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Fngjin b ti d, zhuwi bizhe j g d shji.",
				"vn": "Phng khng rng lm, xung quanh c by vi chic gi sch ln.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Shji l fngzhe hndu sh, yu zhng wn de, yyu wiwn de, yu wnxu, lsh fngmin de, yyu zhngzh, zhxu fngmin de.",
				"vn": "Trn gi sch c by rt nhiu sch, c sch ting Trung, cng c sch nc ngoi, c sch v vn hc, v lch s, cng c sch v mt chnh tr v trit hc.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W knle yxi, hndu sh w d b zhdo, w zh rnsh lxn, mozdng h dngxiopng de mngz, ynwi w zi guni sh dgu tmen de sh, dngrn sh fny chng yngwn de.",
				"vn": "Ti thm quan mt cht, rt nhiu sch ti khng bit, ti ch bit ci tn nh L Tn, Mao Trch ng v ng Tiu Bnh, bi v hi  trong nc ti  tng c sch ca h, ng nhin l sch  dch sang ting Anh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Chung qin sh y zhng xizti, xizti pngbin fngzhe y tixn dinno, dinno hi ki zhene.",
				"vn": "Trc ca s l mt ci bn lm vic, cnh bn lm vic t mt ci my tnh mi, my tnh vn ang bt.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Shf humin de qing shng guzhe j f zhu, tin fng zhzhe zhxi zhu gi wmen jisho shu:",
				"vn": "Trn bc tng sau gh so fa c treo my bc tranh ch, in Phng ch vo nhng bc tranh ch ny gii thiu vi chng ti rng:",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Zh sh xbihng hu de hu er, n sh gumru xi de z.",
				"vn": "y l tranh ca T Bi Hng v, cn kia l ch ca Quch Mt Nhc vit.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng wn w, n zhdo xbihng h gumru sh shi ma?",
				"vn": "in Phng hi ti, bn c bit T Bi Hng v Quch Mt Nhc l ai khng?",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W shu bu zhdo.",
				"vn": "Ti ni l khng bit.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W du zhnggu lioji d ti shole.",
				"vn": "Ti hiu bit qu t v Trung Quc.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng gos w, xbihng sh zhnggu yumng de huji, gumru sh zhnggu yumng de lsh xu ji.",
				"vn": "in Phng ni cho ti bit, T Bi Hng l ha s ni ting ca Trung Quc, Quch Mt Nhc l nh s hc ni ting ca Trung Quc.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng shu:Zhxi sh h zhu du sh w yy li gi wmen de.",
				"vn": "in Phng ni: Nhng cun sch v tranh ch ny u l ng ni  li cho chng ti.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Cngun wn shfng, tin fng shu:Wmen do ktng zu yhuer ba, nmen bsh xing xu bo jiozi ma?",
				"vn": "Tham quan xong th phng, in Phng ni: Chng ta ra ngoi phng khch ngi mt lt i, cc bn chng phi mun hc si co hay sao?",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Jntin, w bba mm qng nmen ch jiozi.",
				"vn": "Hm nay, b m t mi cc bn n si co.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Wmen zu jn ktng de shhu, tin fng de bba mm zhng zhnbi bo jiozi n.",
				"vn": "Lc chng ti bc vo phng khch, b m in Phng ang chun b gi si co.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T bba shu:Nmen zuzhe liotin ba, wmen ling g bo jixngle.",
				"vn": "B c y ni: Cc em c ngi ni chuyn i, hai chng ti gi c ri.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin fng shu:Wmen y qli ba, mik h ml hi xing xu xu ne.",
				"vn": "in Phng ni: Chng ta cng nhau gi vy, Mai C v M L cng mun hc .",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Wmen xle shu ji kish bo jiozi.",
				"vn": "Chng ti ra tay xong th bt u gi si co.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Li zhnggu yhu w chgu y ling c jiozi, dn miyu bogu jiozi, zh sh d y c.",
				"vn": "Sau khi n Trung Quc ti  tng n si co mt vi ln , nhng cha gi si co bao gi, y l ln u tin.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W b hu bo, bole bntin ci bole yg, tin fng shu w bo de jiozi xing xio losh, shu d dji du xiole.",
				"vn": "Ti khng bit gi, gi mi mi gi c mt ci, in Phng ni si co ti gi ging y nh con chut vy, ni lm mi ngi u ci ln.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Wmen dji y bin bo ybin liotin, hn rno.",
				"vn": "Tt c chng ti va gi va ni chuyn, rt vui.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W hn xhun zh zhng hunl de jitng qfn.",
				"vn": "Ti rt thch ci khng kh gia nh vui v ny.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Jiozi dun chlile.",
				"vn": "Si co bng ra ri.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Jntin de jio zi zhn ho ch, w chle rsh du g.",
				"vn": "Si co hm nay tht l ngon. Ti  n hn 20 ci.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ch wn jiozi, wmen yu zule yhuer, ji du ling wi losh shu, wmen gi zule, xixi nmen, wmengu d hn ykui.",
				"vn": "n si co xong, chng ti li ngi chi mt lc, ri ni vi hai thy c, chng em n gi phi v ri, cm n mi ngi, hm nay chng em  chi  y rt vui.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ling wi losh yzh sng wmen zuch dmn, du wmen shu:Hunyng nmen chng li wn er.",
				"vn": "Hai thy c tin chng ti ra tn ngoi ca, ni vi chng ti: Hoan nghnh cc em thng xuyn n chi",
				"from": 0,
				"to": 0
			}
		],
