		"subtitles": [
			{
				"zh": "",
				"py": "D shs k: W de tu bi zxngch zhung shngle",
				"vn": "Chn ca ti b xe p m b thng",
				"from": 3.866,
				"to": 0
			},
			{
				"zh": "",
				"py": " W de tu bi zxngch zhung shngle",
				"vn": "Chn ca ti b xe p m b thng.",
				"from": 13.405,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: N de tu znmele?",
				"vn": "La Lan: Chn ca bn b lm sao?",
				"from": 19.849,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ml: Xngqitin w q ch shng ji de shhu, bi yg xiohuzi zhung do le, cng ch shng shui xili b tu shui shngle.",
				"vn": "M L: Ch nht lc ti p xe ln ph, b mt cu thanh nin m ng, ng t trn xe xung lm chn b au.",
				"from": 22.423,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: Yojn ma?",
				"vn": "La Lan: C nghim trng khng?",
				"from": 33.589,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ml: Lile ydin er xe, byojn.",
				"vn": "M L: Chy mt t mu, khng nghim trng.",
				"from": 35.323,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: Hi tng ma?",
				"vn": "La Lan: Cn au khng?",
				"from": 38.88,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ml: Hi yudin er tng.",
				"vn": "M L: Vn hi au?",
				"from": 40.961,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: Shngzhe gtou lemiyu?",
				"vn": "La Lan: C b thng vo xng khng?",
				"from": 43.372,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "Ml: W bi zhung do hu, xiohuzi mshng jiole ling chz ch, b w sng dole yyun.\r\nDf gi w jinchle yxi, hi ho, mi shngzhe gtou.",
				"vn": "M L: Sau khi ti b m ng, cu thanh nin y gi ngay mt chic xe tc xi, a ti n bnh vin.\r\nBc s khm cho ti mt cht, may qu, khng b thng vo xng.",
				"from": 46.313,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: Ng xiohuzi sh ner de?",
				"vn": "La Lan: Cu thanh nin y l ngi  u?",
				"from": 61.601,
				"to": 0
			},
			{
				"zh": "\r\n\r\n\r\n",
				"py": "Ml: Sh wiy dxu de xushng. \r\nZutin t hi li kngu w.\r\nT y jud tng b hoysi de.\r\nW shu, mishnme, n yu bsh gy de.",
				"vn": "M L: L sinh vin ca i hc ngoi ng.\r\nHm qua anh y cn n thm ti.\r\nAnh y cng cm thy rt y ny.\r\nTi ni, khng sao, anh u phi l c .",
				"from": 65.022,
				"to": 0
			},
			{
				"zh": "",
				"py": "Luln: Ji shng rn du ch y du, q ch shng ji de shhu, ydng yo tbi xioxn.",
				"vn": "La Lan: Trn ng ph, ngi ng, xe c cng ng, nht nh phi ht sc cn thn.",
				"from": 80.41499999999999,
				"to": 0
			},
			{
				"zh": "",
				"py": "Ml: K bsh!",
				"vn": "M L: Phi vy!",
				"from": 88.413,
				"to": 0
			},
			{
				"zh": "",
				"py": "(r) qinbo rng xiotu tu zule",
				"vn": "V tin ca ti b k trm trm mt ri.",
				"from": 93.187,
				"to": 0
			},
			{
				"zh": "(",
				"py": "(Dshn y do le b sho domi sh)",
				"vn": "(i Sn  gp phi khng t chuyn xui xo)",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: Zhn domi!",
				"vn": "i Sn: Xui xo qu!",
				"from": 97.818,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Znmele?",
				"vn": "i c Ha: Sao th?",
				"from": 99.86,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: i, bitle, w de qinbo rng xiotu tu zule.",
				"vn": "i Sn: Ai, ng nhc na, v tin ca ti b k trm  mt ri.",
				"from": 101.61,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Dile dusho qin?",
				"vn": "i c Ha: Mt bao nhiu tin?",
				"from": 109.031,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "Dshn: Qin b du, ci j sh kui qin.\r\nDn w zujn y do hoj jin domi shle.",
				"vn": "i Sn: Tin khng nhiu, mt mi vi chc.\r\nNhng ti do ny gp phi nhng my vic xui xo.",
				"from": 112.19,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Du y do shnme domi shle?",
				"vn": "i c Ha:  gp nhng chuyn xui xo no vy?",
				"from": 120.322,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: W gng mile y ling zxngch, ji jio rn q zule, do xinzi y mi sng huli.",
				"vn": "i Sn: Ti va mi mua c mt ci xe p, lin b ngi ta p i mt ri, n by gi vn cha mang v y tr.",
				"from": 123.981,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: N hi dngzhe gi n sng huli ya?",
				"vn": "i c Ha: Bn cn ang ch ngi ta mang v y tr cho bn ?",
				"from": 133.397,
				"to": 0
			},
			{
				"zh": "\r\n\r\n \r\n \r\n",
				"py": "Dshn: Shng xngq gn pngyu yq q chngchng, chf sh tinq hoho de, mi xingdo, gng do ji xi yle.\r\nY xi d hi tbi d, wmen yu mi di ysn, g g du ln d xing lutngj sh de, yf qundu sh tule.\r\nQintin w zu chz ch yo q shud jchng, chdin er bi sj l do shud jchng.\r\nT shu w de yn f d b zhn, b jchng shu chng jchngle.\r\nN shu k q bk q?",
				"vn": "i Sn: Tun trc i Trng Thnh cng vi bn, lc i tri cn rt p, khng ng, va n ni th tri ma.\r\nCn ma rt to, chng ti u c mang theo , ai cng b t nh chut lt, qun o b t sng ht.\r\nHm trc ti i tc xi mun n nh ht ln, sut na b ti x ch n sn bay th .\r\nNgi li xe ni, ti pht m khng chun, ni t rp ht thnh ph thnh sn bay thnh ph ri.\r\nBn ni xem c tc khng?",
				"from": 137.379,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Znme domi de sh du rng n png shngle?",
				"vn": "i c Ha: Sao bn ton gp nhng chuyn xui xo vy",
				"from": 181.591,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: Suy, qin j tin w y do yg sunmng de, ji jio t gi w sunle yxi",
				"vn": "i Sn: Cho nn, my hm trc ti gp mt ngi thy bi, lin bo ng y xem cho mt qu",
				"from": 186.311,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Jigu znme yng?",
				"vn": "i c Ha: Kt qu th no?",
				"from": 193.89,
				"to": 0
			},
			{
				"zh": "\r\n\r\n\r\n\r\n",
				"py": "Dshn: T shu w jnnin ynq b ti ho, mngnin ji hole.\r\nWile gnxi t, w gile t ybi kui qin.\r\nPngyumen du shu w sh, hu qin shupin.\r\nMi xingdo, nitin gn dji y q q pshn, ynwi zi shnshng chuyn, yu bi gngyun gunl yun fle wsh kui qin.\r\nN shu domi b domi?",
				"vn": "i Sn: ng y ni ti nm nay vn khng tt lm, sang nm mi tt.\r\n t lng cm n ng y, ti a 100 t cho ng y.\r\nBn b ai cng ni ti kh lm, nm tin qua ca s.\r\nKhng ng, hm y i leo ni cng vi mi ngi, bi v ht thuc  trn ni, li b ngi trong cng vin pht mt 50 t.\r\nBn ni xem c nh khng?",
				"from": 196.562,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Yoshi shnshng de sh bi n shozhe, ji gng domile.",
				"vn": "i c Ha: Nu bn t chy cy  trn rng, th cn nh hn.",
				"from": 226.308,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "Dshn: N shu d y du.\r\nSuy cng xi xngq q, w judng b yn ji dio, b chule.",
				"vn": "i Sn: Bn ni ch phi.\r\nCho nn bt u t tun sau, ti quyt nh cai thuc l, khng ht na.",
				"from": 232.296,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Wishme cng xi xngq kish ne?",
				"vn": "i c Ha: V sao li bt u t tun sau ch?",
				"from": 242.664,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: W de yn hi nng chu do xi xngqi, yoshi b chu wn ji lngfile.",
				"vn": "i Sn: Thuc l ca ti cn ht c ti tun sau, nu khng ht th ph lm.",
				"from": 246.927,
				"to": 0
			}
		],
