			{
				"zh": "",
				"py": "Chle gnmo yo ji xing shujio.",
				"vn": "Ung thuc cm mo xong th cm thy bun ng.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Shule ling tin yhu, tu b tngle, dnsh sngzi qu tng qlile.",
				"vn": "Sau khi m hai ngy, u ht au lun, nhng m li bt u au hng.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Df shu, tinq ti gnzo, yu rng w ch yo.",
				"vn": "Bc s ni, tri hanh qu, li bo ti phi ung thuc.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Gule sn tin, sngzi b tngle, yu ksu qlile, rqi yu k yu lhi, k d wnshng shu bzho ju.",
				"vn": "Ba ngy sau, ht au hng, li bt u ho, hn na cng ho cng d di, ho n ni ti khng ng c.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Df knle yhu gi w kile yxi ksu yo.",
				"vn": "Bc s khm xong k cho ti mt t thuc ho.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Mi xingdo, dngtin wnshng w ji fq sho lile.",
				"vn": "Khng ng, ngay ti hm y ti bt u st.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Df zhho yu gi w ki tusho yo.",
				"vn": "Bc s li nh phi k thuc h st cho ti.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T shu zh yo hn ho, dnsh yo du h shu.",
				"vn": "Bc s ni thuc ny rt tt, nhng phi ung nhiu nc vo.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Yo zhn de hn ho, hn kui ji b fshole.",
				"vn": "Thuc tht s rt tt, rt nhanh chng th ht st.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dnsh ho j tin miyu dbinle.",
				"vn": "Nhng my ngy tri khng c cm gic i v sinh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Df y jinch shu w dbin gnzo.",
				"vn": "Bc s va mi khm th ni l ti phn kh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Zh c, df shu ch din er zhngyo ba.",
				"vn": "Ln ny, bc s ni ung mt t thuc bc i.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Zhngyo y hn ho, chle yhu, d r tin ji yule yo dbin de gnju, linmng q csu, mi xingdo yu l q dzi lile.",
				"vn": "Thuc Bc cng rt tt, sau khi ung xong, hm sau th c cm gic mun i v sinh, vi vng i nh x, khng ng li i ngoi.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Q zho df, t shu w b dzi ch huile, zi ki din er zh dzi di yo ch ch ba.",
				"vn": "i tm bc s, bc s ni ti b hng nhu ng rut ri, k thm mt t thuc au bng  ung nh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Zutin xiw, y wi dng yshng de pngyu li kn w.",
				"vn": "Chiu hm qua, mt ngi bn lm bc s n thm ti.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T shu:N de lins znme zhme nnkn, w gi n ki din er yo ch ch ba.",
				"vn": "Anh y ni:  Sao sc mt ca bn kh coi th, Ti k mt t thuc cho bn ung nh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W shu, byngle.",
				"vn": "Ti ni, thi khi cn.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W zh nnkn de lins du sh ch yo ch de.",
				"vn": "Sc mt kh coi ny ca ti u l do ung thuc mi ra nng ni ny.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T hoxing tng b dng w dehu, w ji b zhxi tin knbng de jnggu gn t shule shu, t tngle yhu d xio qli, du w shu, sh, sh, n bnng zi ch yole.",
				"vn": "Anh y hnh nh nghe khng hiu ti ni g, ti mi k ht u ui cu chuyn my hm va ri khm bnh cho anh y nghe, anh y nghe xong ci ph ln, ni vi ti, ng vy, ng vy, anh khng th tip tc ung thuc na.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Bgu, ynggi q yndng yndng.",
				"vn": "C iu, nn i th thao mt cht.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dngtin ti lng, do wibian yndng rngy gnmo, do o t zhngxn q yu yuyng ba.",
				"vn": "Ma ng bn ngoi lnh lm, i ra ngoi th thao d b cm lm, n trung tm Olympic bi mt cht i.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W y tng t dehu, tu yu lk tngle qli.",
				"vn": "Ti va nghe anh y ni xong, u li lp tc thy au tr li.",
				"from": 0,
				"to": 0
			}
