		"subtitles": [
			{
				"zh": "",
				"py": " Yu knnn zho jngch",
				"vn": "C kh khn tm cnh st.",
				"from": 12.15,
				"to": 0
			},
			{
				"zh": "(",
				"py": "(J g tngxu zi liotin, tmen tn do yu knnn zho jngch zh j hu)",
				"vn": "(My ngi bn ang ni chuyn, bn h ni v ci cu ni c kh khn tm cnh st.)",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Mik: Ji shng doch du xizhe yu knnn zho jngch, n zhogu jngch ma?",
				"vn": "Mai C: Khp ni trn ph u vit rng nu nh gp kh khn th tm cnh st, bn  tm cnh st bao gi cha?",
				"from": 15.957,
				"to": 0
			},
			{
				"zh": "",
				"py": "idhu: Miyu.",
				"vn": "i c Ha: Cha bao gi.",
				"from": 24.605,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dshn: Zutin w zi ji shng y do y jin sh.",
				"vn": "i Sn: Hm qua ti  gp mt vic  trn ng i.",
				"from": 26.47,
				"to": 0
			},
			{
				"zh": "",
				"py": "Mik: Shnme sh?",
				"vn": "Mai C: Vic g?",
				"from": 31.761,
				"to": 0
			},
			{
				"zh": "\r\n\r\n\r\n",
				"py": "Dshn: Zi yg shzl ku, w knjinle y du lo fq, kn yngzi sh cng nngcn li de.\r\nTmen yogu ml, dnsh kn do liwng de ch nme du, dngle bntin, y mi gn guli.\r\nZh sh, yg jiotng jngch knjinle, ji lj pole guq, fzhe zh ling wi lorn, yb yb de zu guli.\r\nKn do zh zhng qngjng, w fichng gndng.",
				"vn": "i Sn:  mt ci ng t, ti  nhn thy mt cp v chng gi, nhn dng v l t qu ln.\r\nH mun i qua ng, nhng m nhn thy nhiu xe i qua i li nh vy, ch mt hi lu, cng khng dm i qua.\r\nLc ny, mt anh cnh st giao thng nhn thy, lin lp tc chy qua bn , du hai v ngi ln tui, tng bc tng bc i qua.\r\nNhn thy trng hp ny, ti v cng cm ng.",
				"from": 33.702,
				"to": 0
			},
			{
				"zh": ": ",
				"py": "Mik: Zh sh jngch ynggi zu de sh, yu shn me k gndng de?",
				"vn": "Mai C: y l vic cnh st nn lm, c g m ng cm ng ch.",
				"from": 75.286,
				"to": 0
			},
			{
				"zh": ": ",
				"py": "Dshn: Ksh, yu d dfng, hi fshnggu jngch d rn de sh.",
				"vn": "i Sn: Nhng m, c mt s ni, cn c hin tng cnh st nh dn.",
				"from": 81.551,
				"to": 0
			},
			{
				"zh": ": \r\n\r\n\r\n",
				"py": "idhu: W jud zhnggu de jngch hish bcu de.\r\nW yu g tngxu, qnin qitin q chngqng lxng, b xioxn b qinbo h hzho du dile, shnshang y fn qin y miyule, fichng zhoj.\r\nZhngzi b zhdo znme bn de shhu, t xingdo yu knnn zho jngch zh j hu, ji q zho jngch. Jngch wi t npile zh de dfng, hi ji gi t qin mile hu bijng de jpio, yu kich b t sng do jchng.",
				"vn": "i c Ha: Ti thy cng an Trung Quc rt tt.\r\nTi c mt ngi bn hc, ma thu nm ngoi i du lch Trng Khnh, v khng cn thn lm mt v tin v h chiu, khng cn mt xu no trn ngi, rt st rut.\r\nang trong lc khng bit phi lm th no, anh y ngh ti cu ni nu nh gp kh khn th tm cnh st, lin i tm cnh st.\r\nCnh st sp xp ch  cho anh y, cn cho anh y mn tin mua v my bay v Bc Kinh, li nh xe a anh y n tn sn bay.",
				"from": 89.654,
				"to": 0
			},
			{
				"zh": ":",
				"py": "Mik: Zhn de ma?",
				"vn": "Mai C: C tht khng?",
				"from": 133.86,
				"to": 0
			},
			{
				"zh": ":\r\n\r\n\r\n\r\n\r\n\r\n\r\n",
				"py": "idhu: Dngrn sh zhn de!\r\nW hi zi bo shng kn do zhyng y jin sh: Yg sw su de xionnhir b qi gn do dji shngqle.\r\nT yo po guq n, bi jngch knjinle.\r\nJngch ji bng hizi b qi jinle huli, rnhu b xio nn hi bo do l bin.\r\nHizi shule yshng xixi shshu, gng yo zu, yu huli du jngch shu:Shshu, w de xi di kile.\r\nShuzhe ji b xiojio shn do jngch minqin, jngch xiozhe wn xiyo q, gi hizi b xi di x ho.\r\nZh sh hizi de mm y pole guli, kn do zh zhng qngjng, gndng d b zhdo shu shnme ho.",
				"vn": "i c Ha: ng nhin l tht!\r\nTi cn c c mt chuyn nh th ny  trn bo:\r\nmt b trai khong bn nm tui ln qu bng ra ngoi ng ci.\r\nN nh chy qua  ly, b anh cnh st nhn thy.\r\nAnh cnh st lin gip cu b nht qu bng v, sau  b cu b vo trong va h.\r\nB trai ni mt ting cm n thc thc, ang nh i, li quay li ni vi cnh st rng: ch i, dy giy ca chu bung ra ri .\r\nVa ni va th bn chn nh xu ra trc mt cnh st, cnh st va ci va ci lng xung, tht cht dy giy cho cu b.\r\nLc ny m a b cng chy ti, nhn thy trng hp ny, cm ng chng bit phi ni g hn.",
				"from": 136.122,
				"to": 0
			},
			{
				"zh": "",
				"py": "(r) wmen b sngzhmi jiozu su hn snyu",
				"vn": "(2) Chng ti gi tng trc mai l ba ngi bn ma ng",
				"from": 205.362,
				"to": 0
			},
			{
				"zh": "(",
				"py": "(Zi wng losh ji de ktng l, wng losh zhng y hngu pngyu pio zhngho xinshng tnhu)",
				"vn": "( trong phng khch nh thy Vng, Thy Vng ang cng ngi bn Hn Quc Phiu Chnh Ho ni chuyn)",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Pio zhngho: Zh f hng mi t hu d zhn ho!",
				"vn": "Phiu Chnh Ho: Bc tranh (Hng Mai ) ny v p qu!",
				"from": 211.419,
				"to": 0
			},
			{
				"zh": "",
				"py": "Wng losh: Zh sh y wi huji pngyu sng de.",
				"vn": "Thy Vng: y l tranh ca mt ngi bn ha s tng.",
				"from": 216.947,
				"to": 0
			},
			{
				"zh": "",
				"py": "Pio zhngho: Surn sh dngtin, dnsh y kn do zh f hu ji gndo xing chntin yyng.",
				"vn": "Phiu Chnh Ho: Mc d l ma ng, nhng m ch cn nhn thy bc tranh ny th cm thy nh l ma xun vy.",
				"from": 221.199,
				"to": 0
			},
			{
				"zh": "\r\n\r\n",
				"py": "Wng losh: Mi, sng, zh sh zhnggu huji zu xhun hu de.\r\nZhnggu rn b sngzhmi jiozu su hn snyu. Tng shu xinshng hn xhun zhngguhu h zhnggu shf.",
				"vn": "Thy Vng: Mai, Tng, Trc l cc ha s ngi Trung thch v nht.\r\nNgi Trung gi Tng, Trc, Mai l tu hn tam hu.\r\nNghe ni tin sinh rt thch tranh Trung Quc v th php Trung Quc.",
				"from": 230.185,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "Pio zhngho: Sh.\r\nW mi c do zhnggu li, kn do xhun de zhu, zng yomi yxi di huq.",
				"vn": "Phiu Chnh Ho: Vng.\r\nMi ln ti n Trung Quc, nhn thy tranh ch m mnh thch, lun phi mua mt t  mang v nc.",
				"from": 248.905,
				"to": 0
			},
			{
				"zh": "",
				"py": "Wng losh: W gn nn yyng, y fichng xhun shf h zhngguhu.",
				"vn": "Thy Vng: Ti v anh ging nhau, cng v cng thch th php v tranh Trung Quc.",
				"from": 258.601,
				"to": 0
			},
			{
				"zh": "",
				"py": "Pio zhngho: W kn nn de z xi de hn pioliang.",
				"vn": "Phiu Chnh Ho: Ti thy ch thy vit rt p.",
				"from": 265.402,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "Wng losh: Nl. Nn gujingle.",
				"vn": "Thy Vng: u c.\r\nAnh qu khen ri.",
				"from": 269.654,
				"to": 0
			},
			{
				"zh": "",
				"py": "Shzl ku",
				"vn": "Ng t",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Tin kui hile.",
				"vn": "Tri sp ti ri.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W qzhe zxngch, j ji mng mang de wng ji zu.",
				"vn": "Ti ang p xe, vi vi vng vng i v nh.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Dole shzl ku de shhu, qinmin hng dng lingle.",
				"vn": "Lc n ch n xanh n , n   pha trc sng ln.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W jmng tngch, dnsh ch mi tng zh, hish xing qin po, w jn w ch zh, hish bxng, ch zh huile!",
				"vn": "Ti vi vng dng xe, nhng xe khng dng li c, vn lao v pha trc, ti bp cht phanh, vn khng dng li c, phanh b hng ri!",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W linmng wng xi tio, dnsh hishgule tngch xin.",
				"vn": "Ti vi vng nhy xung, nhng m vn vt qu vch dng xe.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Yg jngch zhng zhn zi w minqin, t xing w jngle g l shu:Xinshng, qng n b zxngch tu do n bin q.",
				"vn": "Mt anh cnh st ang ng trc mt ti, anh y hng ti cho l mt ci ni rng: cho anh, mi anh dt xe p qua bn kia.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T zhle zh l bin de gngtng.",
				"vn": "Anh y ch vo ci bt bn ng.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Shnme,gu ydin er y yo fkun?",
				"vn": "ci g, qua c mt t m cng i pht ?",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "W xnl hn b goxng, shu:Zhgu zhme y xio din er, w yhu ydng znshu jiotng guz, b zi",
				"vn": "Trong lng ti rt khng vui, ni:  ch qua c mt t nh th ny, ti t nay v sau nht nh s tun th lut giao thng, khng bao gi",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "Xinshng, qng n guq!",
				"vn": "Anh i, mi anh qua bn kia!",
				"from": 0,
				"to": 0
			},
			{
				"zh": "",
				"py": "T yu jngle g l, hn lmo.",
				"vn": "Anh y li cho l ti mt ci, rt l php.",
				"from": 0,
				"to": 0
			},
			{
				"zh": "\r\n",
				"py": "W zhho b ch tu guq. T cng gngtng l n chli yg gngj xing, qch y b qinzi, shu:W kn n de ch zh huile, suy di gi n xil yxi, dji shng hi yu hodu g shzl ku ne!",
				"vn": "Ti nh phi dt xe qua bn y.\r\nAnh y ly mt ci hm dng c t trong bt ra, ly ra mt ci km, ni: Ti thy phanh ca anh b hng ri, cho nn phi sa gip anh mt cht, trn ng ph cn rt nhiu ci ng t na!",
				"from": 0,
				"to": 0
			}
		],
